    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 0.5rem;\r\n  }\r\n  header a {\r\n    color: #ffffff;\r\n    text-decoration: none;\r\n    padding-right: 1rem;\r\n  }\r\n  header a:hover {\r\n    color: #f08040;\r\n  }\r\n  .brand a {\r\n    font-weight: bold;\r\n    font-size: 3rem;\r\n    padding-left: 1rem;\r\n  }\r\n  footer {\r\n    background-color: #203040;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    color: #ffffff;\r\n  }\r\n  /* Products */\r\n  .products {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    list-style-type: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    justify-content: center;\r\n  }\r\n  .products li {\r\n    flex: 0 1 30rem;\r\n    margin: 1rem;\r\n    margin-bottom: 0;\r\n    height: 42rem;\r\n    border-top: 0.2rem #e0e0e0 solid;\r\n  }\r\n  .product {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-around;\r\n    height: 100%;\r\n  }\r\n  .product img {\r\n    max-height: 26rem;\r\n    max-width: 22rem;\r\n    margin-top: 2rem;\r\n  }\r\n  .product-name {\r\n    line-height: 2.2rem;\r\n  }\r\n  .product-brand {\r\n    font-size: 1.2rem;\r\n    color: #808080;\r\n  }\r\n  .product-price {\r\n    font-size: 2rem;\r\n  }\r\n  .product-price span {\r\n    font-size: 1.2rem;\r\n  }\r\n  /* Rating */\r\n  .rating {\r\n    color: #ffc000;\r\n    font-size: 1.8rem;\r\n  }\r\n  .rating span:last-child {\r\n    color: #444444;\r\n    font-size: 1.4rem;\r\n  }\r\n  .content {\r\n    padding: 1rem;\r\n  }\r\n  /* Product Details */\r\n  .details {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: flex-start;\r\n    flex-wrap: wrap;\r\n  }\r\n  .details-image {\r\n    flex: 2 1 60rem;\r\n  }\r\n  .details-image img {\r\n    max-width: 60rem;\r\n    width: 100%;\r\n  }\r\n  .details-info,\r\n  .details-action {\r\n    flex: 1 1 30rem;\r\n  }\r\n  .details-info ul,\r\n  .details-action ul {\r\n    padding: 0;\r\n    list-style-type: none;\r\n  }\r\n  .details-info h1 {\r\n    font-size: 2rem;\r\n    margin: 0;\r\n  }\r\n  .details-info li,\r\n  .details-action li {\r\n    margin-bottom: 1rem;\r\n  }\r\n  .details-action {\r\n    border: 0.1rem #808080 solid;\r\n    border-radius: 0.5rem;\r\n    background-color: #f8f8f8;\r\n    padding: 1rem;\r\n  }\r\n  /* Cart */\r\n  .cart {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    align-items: flex-start;\r\n  }\r\n  \r\n  .cart-list {\r\n    flex: 3 1 60rem;\r\n  }\r\n  .cart-action {\r\n    flex: 1 1 20rem;\r\n    background-color: #f0f0f0;\r\n    border-radius: 0.5rem;\r\n    padding: 1rem;\r\n  }\r\n  .cart-list-container {\r\n    padding: 1rem;\r\n    list-style-type: none;\r\n  }\r\n  \r\n  .cart-list-container li {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding-bottom: 1rem;\r\n    margin-bottom: 1rem;\r\n    border-bottom: 0.1rem #c0c0c0 solid;\r\n  }\r\n  .cart-list-container img {\r\n    max-width: 10rem;\r\n    max-height: 10rem;\r\n  }\r\n  .cart-list-container li:first-child {\r\n    align-items: flex-end;\r\n  }\r\n  .cart-image {\r\n    flex: 1 1;\r\n  }\r\n  .cart-name {\r\n    flex: 8 1;\r\n  }\r\n  .cart-price {\r\n    flex: 1 1;\r\n    text-align: right;\r\n  }\r\n  .cart-name > div {\r\n    padding: 1rem;\r\n  }\r\n  .cart-list h3 {\r\n    margin: 0;\r\n  }\r\n  .cart-list button,\r\n  .cart-list select {\r\n    font-size: 1.3rem;\r\n    padding: 0.5rem;\r\n  }\r\n  /* Form */\r\n  .form-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: flex-start;\r\n    height: 100%;\r\n  }\r\n  .form-items {\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: 32rem;\r\n    padding: 2rem;\r\n    border: 0.1rem #c0c0c0 solid;\r\n    border-radius: 0.5rem;\r\n    list-style-type: none;\r\n  }\r\n  .form-items li {\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin-bottom: 1rem;\r\n    margin-top: 